import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from "react-helmet";

export default class Add3DViewerToWixBlog extends React.Component {
    render() {
        return (
            <div className='blog-page'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Showcase products in 3D and augmented reality on your Wix site</title>
                    <link rel="canonical" href="https://www.ezyvue.com/blog/add3DViewerToWix" />
                </Helmet>
                <main>
                    <h1>Showcase products in 3D and augmented reality on your Wix site</h1>
                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/jewelrystore.jpg')} loading='lazy' />
                    <p style={{ lineHeight: '1.0' }}>David Long<br />
                        <span style={{ fontSize: 'small' }}>10th May 2023</span>
                    </p>
                    <hr />
                    <p>
                        With the Ezyvue platform your customers can view and interact with a 3D model of a product and see it in their home with augmented reality (AR).
                        The following guide will take you through each step to add the Ezyvue 3D viewer to a Wix page and display a 3D model.
                        <a href='https://www.wix.com/'> Wix </a> is a website builder with an easy-to-use drag-and-drop editor that lets you create a website or online store for free. 
                    </p>
                    <ol>
                        <li>Create a free/trial Ezyvue account</li>
                        <p>
                            Go to the <Link to='/pricing'>pricing page</Link> and sign up.
                            Click on the link in the email sent to you to activate your account and you&apos;re ready to log in.
                        </p>
                        <li>Create a new project and upload a 3D model</li>
                        <p>
                            From the Project menu select New Project.
                            Fill in the name and description and select a single file or a directory if the 3D model has additional asset files such as textures.
                            Hit the Submit button and a new project will be created which can be viewed on the Manage Projects page.
                        </p>
                        <li>Preview the file</li>
                        <p>
                            On the Manage Projects page, click the eye icon next to the project to preview the model.
                        </p>
                        <li>Change the placeholder loading background</li>
                        <p>
                            The default loading image for the viewer is a placeholder image. To change this, load the Ezyvue Studio by clicking the edit button (pencil icon) next to the project on the Manage Products page.
                            When the model loads you'll notice the grid is visible, we don't want this in the image, so click the viewport tab (square icon) and untick the grid checkbox.
                            Next, click the General Settings tab (the gear icon) and adjust the camera in the main viewport then click the Take Snapshot button.
                        </p>
                        <li>Embed the viewer on your Wix site</li>
                        <p>
                            For this guide, I've created a simple site from a jewelry store template.
                            <ol>
                                <li>
                                    Click on the Add Section Button
                                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/wix/addsection.jpg')} loading='lazy' />
                                </li>
                                <li>
                                    With the section selected, click the add button on the command bar. In the Add Elements window choose a blank white Strip.
                                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/wix/newsection.jpg')} loading='lazy' />
                                </li>
                                <li>
                                    Right mouse click on the strip and select Add column in the context menu. This will split the strip into two columns.
                                    Click on the left column and click the add button on the command bar. In the Add Elements window click Embed Code and then choose Embed HTML.
                                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/wix/embedcodeelement.jpg')} loading='lazy' />
                                    An HTML Embed element will be added to the page.
                                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/wix/embedsettings.jpg')} loading='lazy' />
                                </li>
                                <li>
                                    Go to the Integrate Viewer page on Ezyvue and in the HTML Code section and select a project.
                                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/shared/code.jpg')} loading='lazy' />
                                    Click the copy button below the Html Code window, paste it into the HTML Settings dialog and click the Update button.
                                    <img alt='ten uses of augmented reality' src={require('assets/images/blog/wix/codeadded.jpg')} loading='lazy' />
                                    The viewer should load with the project image displayed.
                                </li>
                            </ol>
                        </p>
                        <li>Save and preview your site</li>
                        <p>
                            Firstly save the site by clicking the Save button on the main menu bar.
                            Click the Preview button next to the Save button and admire your work!
                            <img alt='ten uses of augmented reality' src={require('assets/images/blog/wix/preview.jpg')} loading='lazy' />
                        </p>
                    </ol>
                    <hr />
                    <h3>Related blogs</h3>
                    <Link to='/blog/enhanceWebsiteWithA3DViewer'>Enhancing your online store with a 3D viewer</Link>
                    <br />
                    <Link to='/blog/addingA3dViewerToAWebsite'>Display products in 3D and AR on your website</Link>
                    <br />
                    <br />
                </main>
            </div>
        );
    }
}